<template>
  <div id="resource">
    <div class="title">资源管理</div>
    <div class="list" v-if="listShow">
      <div class="title" style="font-size: 18px;margin-top: 40px;">视频管理</div>
      <div class="videoBox">
        <p class="videoTitle">视频列表</p>
        <div class="videoList">
          <div class="videoItem" v-for="(item,i) in uploadData" :key="i">
            <div class="num">{{ i+1 }}</div>
            <div class="item">
              <img src="../../assets/images/index/icon_video.png" alt="" class="icon_left">
              <p class="time">{{ item.videoName }} | 上传时间{{ item.createTime }}</p>
              <div class="del">
                <div class="colLine"></div>
                <img src="../../assets/images/index/icon_delete.png" alt="" class="icon_right" @click="getDeleteName(item)">
              </div>
            </div>
          </div>
          <div class="uploadVideo" @click="toAdd(0)">
            <img src="../../assets/images/index/icon_upload1.png" alt="" class="icon">
            <p class="tips">点击上传视频</p>
          </div>
        </div>
      </div>
      <div class="title" style="font-size: 18px;margin-top: 40px;">下载资源管理</div>
      <div class="downloadBox">
        <div class="downloadItem" v-for="(item,i) in downloadData" :key="i">
          <div class="titleBox">{{ item.videoName }}</div>
          <div class="center">
            <p class="time">上传时间：{{ item.createTime }}</p>
            <!--<img src="../../assets/images/index/icon_delete.png" alt="" class="icon" @click="dialogVisible=true">-->
          </div>
          <div class="update" @click="toAdd(1)">
            <img src="../../assets/images/index/icon_upload2.png" alt="" class="icon">
            <p class="tips">点击更新</p>
          </div>
        </div>
      </div>
    </div>
    <div class="addFile" v-else>
      <div class="add" v-show="status1===1">
        <div class="title" style="font-size: 18px;margin-top: 40px;">视频管理</div>
        <div class="box">
          <div class="upload">
            <!--<el-form :model="video">
              &lt;!&ndash;<el-form-item>
                <el-input v-model="video.name" placeholder="视频名称"></el-input>
              </el-form-item>&ndash;&gt;
              <el-form-item>
                <el-upload
                    class="avatar-uploader"
                    action=""
                    :show-file-list="false"
                    :before-upload="beforeAvatarUpload"
                >
                  &lt;!&ndash;<video
                      v-if="videoUrl"
                      :src="videoUrl"
                      class="avatar"
                      id="tempImg"
                  ></video>&ndash;&gt;
                  <i class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
              </el-form-item>
            </el-form>-->
            <input type="file" name="file" id="video" @change="uploadVideo" accept="video/*" />
            <img src="../../assets/images/index/icon_upload3.png" alt="" class="icon">
            <p class="tips">点击上传视频</p>
          </div>
          <el-input v-model="video.name" placeholder="请输入视频名字" class="text" style="margin-bottom: 20px;"></el-input>
          <el-input
              :rows="12"
              type="textarea"
              class="text"
              v-model="video.describe"
              placeholder="请在这里输入相关的视频说明文字"></el-input>
          <div class="btnBox">
            <div class="btn" @click="cancel1()">取消上传</div>
            <div class="btn" @click="upload()">确认上传</div>
          </div>
        </div>
      </div>
      <div class="add" v-show="status2===1">
        <div class="title" style="font-size: 18px;margin-top: 40px;">固件管理</div>
        <div class="box">
          <div class="upload">
            <input type="file" name="file" id="zip" @change="updateZip" accept=".zip,.rar" />
            <img src="../../assets/images/index/icon_upload3.png" alt="" class="icon">
            <p class="tips">点击上传固件</p>
          </div>
          <el-input v-model="zip.name" placeholder="请输入固件名字" class="text" style="margin-bottom: 20px;"></el-input>
          <el-input
              :rows="12"
              type="textarea"
              class="text"
              v-model="zip.describe"
              placeholder="请在这里输入相关的固件说明文字"></el-input>
          <div class="btnBox">
            <div class="btn" @click="cancel2()">取消上传</div>
            <div class="btn" @click="update()">确认上传</div>
          </div>
        </div>
      </div>
    </div>
    <el-dialog
        title="确认删除"
        :visible.sync="dialogVisible"
        width="25%"
        class="dialog"
        :lock-scroll="false"
        top="30vh"
        center>
      <div class="tips">
        删除后将无法恢复，是否确认删除本资源？
      </div>
      <div slot="footer" class="dialog-footer">
        <div class="btn" @click="dialogVisible = false">取消</div>
        <div class="btn" @click="deleteResource()">确认</div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
/*  import put from '../../api/resource'*/

  export default {
    name: "Resource",
    data() {
      return {
        uploadData: [],
        downloadData: [],
        dialogVisible: false,
        listShow: true,
        status1: 0,
        status2: 0,
        video: {
          describe: '',
          file: '',
          name: ''
        },
        zip: {
          describe: '',
          file: '',
          name: ''
        },
        deleteName: ''
      }
    },
    methods: {
      toAdd(status) {
        if(status) {
          this.status2 = 1;
        }else {
          this.status1 = 1;
        }
        this.listShow = false;
      },
      getDeleteName(i) {
        this.deleteName = i.videoName;
        this.dialogVisible = true;
      },
      deleteResource() {
        this.$axios.delete('/file/video',{
          data: {
            videoName: this.deleteName
          }
        }).then((res) => {
          console.log(res);
          if(res.status === 200) {
            this.dialogVisible = false;
            this.$message.success(res.data);
            this.getVideo();
          }
          else {
            this.$message.error(res.msg);
          }
        }).catch((err) => {
          console.log(err);
        })
      },
      uploadVideo(e) {
        let file = e.target.files[0];
        this.video.file = file;
        /*return false;*/
      },
      upload() {
        let formData = new FormData();
        formData.append('describe',this.video.describe);
        formData.append('videoName',this.video.name);
        formData.append('file',this.video.file);
        console.log(formData.get('file')); //FormData私有类对象，访问不到，可以通过get判断值是否传进去

        this.$axios.post('/file/video',formData,{
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        }).then((res) => {
          console.log(res);
           if(res.status === 200) {
             this.$message.success(res.data);
             this.clearVideo();
             this.getVideo();
             this.status2 = 1;
             this.listShow = true;
           }
           else {
             this.$message.error(res.msg);
           }
        }).catch((err) => {
          console.log(err);
        })
      },
      cancel1() {
        this.listShow = true;
        this.status1 = 0;
        this.clearVideo();
      },
      cancel2() {
        this.listShow = true;
        this.status2 = 0;
        this.clearZip();
      },
      updateZip(e) {
        let file = e.target.files[0];
        this.zip.file = file;
      },
      update() {
        let formData = new FormData();
        formData.append('describe',this.zip.describe);
        formData.append('zipName',this.zip.name);
        formData.append('file',this.zip.file);
        console.log(formData.get('file')); //FormData私有类对象，访问不到，可以通过get判断值是否传进去

        this.$axios.post('/file/zip',formData,{
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        }).then((res) => {
          if(res.status === 200) {
            this.$message.success(res.data);
            this.getZip();
            this.status2 = 0;
            this.clearZip();
            this.listShow = true;
          }
          else {
            this.$message.error(res.msg);
          }
        }).catch((err) => {
          console.log(err);
        })
      },
      getVideo() {
        this.$axios.get('/file/video2',).then((res) => {
          if(res.status === 200) {
            console.log(res.data);
            this.uploadData = res.data;
          }
          else {
            this.$message.error(res.msg);
          }
        }).catch((err) => {
          console.log(err);
        })
      },
      getZip() {
        this.$axios.get('/file/zip').then((res) => {
           if(res.status === 200) {
             console.log(res.data);
             this.downloadData = res.data;
           }
           else {
             this.$message.error(res.msg);
           }
        }).catch((err) => {
          console.log(err);
        })
      },
      clearVideo() {
        this.video.describe = '';
        this.video.file = '';
        this.video.name = '';
      },
      clearZip() {
        this.zip.describe = '';
        this.zip.file = '';
        this.zip.name = '';
      }
    },
    beforeMount() {
      this.getVideo();
      this.getZip();
    },
    beforeRouteLeave(to,from,next) {
      this.clearVideo();
      this.clearZip();
      this.listShow = true;
      this.status1 = 0;
      this.status2 = 0;
      next();
    }
  }
</script>

<style lang="less" scoped>
  @import "../../assets/style/resource/resource";
</style>
